import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'grafico-recomendacao',
  templateUrl: './recomendacao.component.html',
  styleUrls: ['./recomendacao.component.scss']
})
export class RecomendacaoComponent implements OnInit {
  
  public data: any;
  public options: any;
  
  @Input() dados: any;
  
  constructor() {
    
    this.data = {
      labels: ['Bovinos','Ovinos','Pescados'],
      datasets: [
        {
          data: [0, 0, 0, 0, 0],
          backgroundColor: [
            "#9CC4E4",
            "#3A89C9",
            "#2B4E72",
          ],
          hoverBackgroundColor: [
            "#F15500",
            "#F15500",
            "#F15500",
          ]
        }]    
      };
      
      this.options = {
        title: {
          display: false,
        },
        legend: {
          display: false,
        }
      };
      
    }
    
    ngOnInit() {
      this.data.datasets[0].data = this.dados;
    }
    
  }
  