import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'grafico-faturamento',
  templateUrl: './faturamento.component.html',
  styleUrls: ['./faturamento.component.scss']
})
export class FaturamentoComponent implements OnInit {

  public data: any;
  public options: any;
  
  @Input() dados: any;
  
  constructor() {
    
    this.data = {
      labels: ['Bovino','Ovinos'],
      datasets: [
        {
          data: [100, 10],
          backgroundColor: [
            "#9CC4E4",
            "#3A89C9"
          ],
          hoverBackgroundColor: [
            "#F15500",
            "#F15500"
          ]
        }]    
      };
      
      this.options = {
        title: {
          display: false,
        },
        legend: {
          display: false,
        }
      };
      
    }
    
    ngOnInit() {
      this.data.datasets[0].data = this.dados;
    }
    
  }
  