import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'grafico-comparativo',
  templateUrl: './comparativo.component.html',
  styleUrls: ['./comparativo.component.scss']
})
export class ComparativoComponent implements OnInit {

  public data: any;
  public options: any;
  
  @Input() dados: any;
  
  constructor() {
    
    this.data = {
      labels: ['Jul', 'Ago', 'Set', 'Out','Nov','Dez','Jan'],
      datasets: [
          {
              label: '2018',
              data: [5000, 6000, 1200, 8000, 3000, 4000, 2000],
              fill: false,
              borderColor: '#355C7D'
          },
          {
              label: '2019',
              data: [3000, 4000, 9000, 7000, 6000, 3000, 9000],
              fill: false,
              borderColor: '#00AAFF'
          }
      ]
  };

      this.options = {
        title: {
          display: false,
        },
        legend: {
          position: 'bottom'
        }
      };
      
    }
    
    ngOnInit() {
      this.data.datasets[0].data = this.dados[0];
      this.data.datasets[1].data = this.dados[1];
    }
    
  }
  